.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0; }

.sr-only-focusable:active, .sr-only-focusable:focus {
  position: static;
  width: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  clip: auto; }

body {
  font-family: 'Kurale', serif; }

img {
  max-height: 100%;
  max-width: 100%; }

section {
  position: relative;
  padding: 0;
  color: #FFF;
  text-align: center; }

section::before,
section::after {
  position: absolute;
  content: ''; }

.ss-style-top::before {
  left: 0;
  width: 100%;
  height: 30px;
  background-size: 25px 100%;
  top: 0;
  background-image: linear-gradient(315deg, #fbfbfb 50%, transparent 50%), linear-gradient(45deg, #fbfbfb 50%, transparent 50%);
  margin-top: -30px;
  z-index: 100; }

.ss-style-bottom::before {
  left: 0;
  width: 100%;
  height: 30px;
  background-size: 25px 100%;
  top: 0;
  background-image: linear-gradient(583deg, #fbfbfb 50%, transparent 50%), linear-gradient(136deg, #fbfbfb 50%, transparent 50%);
  margin-top: 0px;
  z-index: 100; }

.bg-overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(44, 33, 5, 0.2);
  z-index: 0; }

.light-wrapper {
  background: #fbfbfb;
  color: #222; }

.inner {
  padding-top: 112px;
  padding-bottom: 32px; }

.dark-wrapper {
  background: #ed0121;
  color: white;
  font-size: 20px;
  font-weight: bold; }
  .dark-wrapper .ss-style-top::before {
    background-image: linear-gradient(315deg, #ed0121 50%, transparent 50%), linear-gradient(45deg, #ed0121 50%, transparent 50%); }

@keyframes jump {
  0% {
    transform: translate3d(0, 0, 0); }
  40% {
    transform: translate3d(0, 30%, 0); }
  100% {
    transform: translate3d(0, 100%, 0); } }

.jump {
  transform-origin: 50% 50%;
  animation: jump .5s linear alternate infinite; }

